import { NgModule } from '@angular/core';
import { SharedModule } from '@shared';
import { NgxEchartsModule } from 'ngx-echarts';


import {
    TableModule
} from 'primeng/table';
import { ToolbarModule } from 'primeng/toolbar';

import { HisRoutingModule } from './his-routing.module';
import { CommonComponentModule } from '../common-component/common.module';
import { MainComponent } from './main/main.component';


const COMPONENTS = [MainComponent];


@NgModule({
    imports: [SharedModule, HisRoutingModule, NgxEchartsModule, ToolbarModule,
        TableModule, CommonComponentModule],
    declarations: [...COMPONENTS],
    entryComponents: COMPONENTS,
})
export class HisModule { }
